<template>
<v-app>
<v-card class="justify-space-between mx-auto">
  <v-simple-table>
      <template v-slot:default>
        <thead>
          <tr>
            <th class="text-left">
              ID
            </th>
            <th class="text-left">
              NOMBRE
            </th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="item in user"
            :key="item.id"
          >
            <td>{{ item.id }}</td>
            <td>{{ item.nombre }}</td>
          </tr>
        </tbody>
      </template>
    </v-simple-table>
</v-card>
</v-app>
</template>
<script>
import axios from "axios";

export default {
  data(){
    return{
      user:[]
    }
  },
  mounted: function() {
    axios.get("http://localhost:8080/home").then(r => {
      this.user = r.data;
    })
  },
}
</script>
